          ></v-text-field>
        </v-col>
        <p><span class="red--text">*</span>第二步：我的项目类型（可多选）</p>
        <v-row>
          <v-checkbox
            v-model="jixie"
            class="ml-4 mx-2"
            label="机械"
          ></v-checkbox>
          <v-checkbox v-model="dianqi" class="mx-2" label="电气"></v-checkbox>
          <v-checkbox
            v-model="nengyuan"
            class="mx-2"
            label="能源动力"
          ></v-checkbox>
          <v-checkbox
            v-model="dianzi"
            class="mx-2"
            label="电子信息"
          ></v-checkbox>
          <v-checkbox
            v-model="cailiao"
            class="mx-2"
            label="新材料"
          ></v-checkbox>
          <v-checkbox v-model="huagong" class="mx-2" label="化工"></v-checkbox>
          <v-checkbox
            v-model="shengwu"
            class="mx-2"
            label="生物医疗"
          ></v-checkbox>
          <v-checkbox
            v-model="hangkong"
            class="mx-2"
            label="航空航天"
          ></v-checkbox>
          <v-checkbox v-model="qita" class="mx-2" label="其他"></v-checkbox>
          <v-col cols="12" sm="6">
            <v-text-field
