            placeholder="请输入您的资金预算（元）"
            v-model="projectName"
          ></v-text-field>
        </v-col>
        <div class="mt-5"><span class="red--text">*</span>第五步：联系方式</div>
        <v-col cols="12" sm="6">
          <v-text-field
            required
            filled
            label="联系人姓名"
            placeholder="请输入联系人姓名"
            v-model="contactName"
          ></v-text-field>
          <v-text-field
            required
            filled
            label="联系人电话"
            placeholder="请输入联系人电话"
            v-model="contactPhone"
          ></v-text-field>
          <v-text-field
            required
            filled
            label="联系邮箱"
            placeholder="请输入联系邮箱"
            v-model="contactEmail"
          ></v-text-field>
        </v-col>
        <v-row class="mt-8 mb-12 ml-2">
          <div class="mr-12 ml-3">
            <v-btn
              width="120px"
              depressed
              large
              color="info"
              :loading="loading"
              :disabled="loading"
              >发布需求</v-btn
            >
          </div>
          <div class="mr-12">
            <v-btn
              width="120px"
              class="white--text"
              depressed
              large
              color="#AAAAAA"
              >取消</v-btn
            >
          </div>
        </v-row>
      </v-form>
