      contactName: '',
      contactPhone: '',
      contactEmail: '',
      personalContact: '',
      projectName: '',
      uniName: '',
      jixie: false,
      dianqi: false,
      nengyuan: false,
      dianzi: false,
      cailiao: false,
      huagong: false,
      shengwu: false,
      hangkong: false,
      qita: false,
      breadList: [
        {
          text: '您的位置：首页',
          disabled: false,
          href: '/company'
        },
        {
          text: '发布科研成果',
          disabled: true
        }
      ]
    }
  },
  methods: {
    onEditorChange({ html }) {
      this.content = html
    }
  }
}
</script>

<style scoped>
.back {
  background-color: #f2f2f2;
}
</style>
